const slider = document.querySelector('.swiper-container');

let mySwiper = new Swiper(slider, {
	slidesPerView: 1,
	spaceBetween: 30,
	loop: true,
	pagination: {
		el: '.swiper-pagination',
		clickable: true,
	},
	navigation: {
		nextEl: '.swiper-button-next',
		prevEl: '.swiper-button-prev',
	},
	breakpoints: {
		992: {
			slidesPerView: 2,
		}
	}
})

let mySwipers;


const sliders = document.querySelector('.slider-container');
function mobileSlider() {
	if (window.innerWidth <= 545 && sliders.dataset.mobile == 'false') {
		mySwipers = new Swiper(sliders, {
			slidesPerView: 1.25,
			spaceBetween: 10,
			loop: true,
			slideClass: 'product__item',
			// pagination: {
			// 	el: '.swiper-pagination',
			// 	clickable: true,
			// },
		});

		sliders.dataset.mobile = 'true';
	}

	if (window.innerWidth > 545) {
		sliders.dataset.mobile = 'false';
		if (sliders.classList.contains('swiper-container-initialized')) {
			mySwipers.destroy();
		}
	}
}

mobileSlider()

window.addEventListener('resize', () => {
	mobileSlider();
});

